<template >
  <div id="nuevoEmp">
      <h3>New Employee</h3>
      <div class="row">
      <form @submit.prevent="Agregar" class="col s12">
        <div class="row">
          <div class="input-field col s12">
            <input type="number" v-model="empleados_id" required>
            <label>ID del empleado </label>
          </div>
        </div>
        <div class="row">
          <div class="input-field col s12">
            <input type="text" v-model="nombre" required>
            <label> Nombre</label>
          </div>
        </div>
        <div class="row">
          <div class="input-field col s12">
            <input type="text" v-model="departamento" required>
            <label>Departamento</label>
          </div>
        </div>
        <div class="row">
          <div class="input-field col s12">
            <input type="text" v-model="posicion" required>
            <label>Posicion </label>
          </div>
        </div>
        <button type="submit" class="btn">Submit</button>
        <router-link to="/" class="btn grey">Cancel</router-link>
      </form>
    </div>
    </div>

</template>

<script>
import db from './firebaseinit'
export default {
  name:'nuevoEmp',
  data (){
    return {
      empleados_id: null,
       Nombre: null,
       departamento: null,
       posicion: null
    }
  },
  methods: {
       Agregar() {
         db.collection('Empleados').add({
           NumeroEmp: this.empleados_id,
           Nombre: this.nombre,
           Departamento: this.departamento,
           sha: this.posicion
         })
         .then(docRef => {
           console.log('Client added: ', docRef.id)
           this.$router.push('/')
         })
         .catch(error => {
           console.error('Error agregando el empleado: ', error)
         })
       }
     }
}
</script>



