<template>
  <div id="dashboard">
    <ul class="collection with-header">
      <li class="collection-header"><h3>Listado de Empleados</h3></li>
       <li v-for=" empleados in empleadoA"
         v-bind:key="empleados.id"
           class="collection-item">
            <div class="chip">
               {{empleados.departamento}}
            </div>
            {{empleados.nombre}}
            {{empleados.empleados_id}}
            {{empleados.posicion}}
             <router-link class="secondary-content" :to="{ name: 'mostrandoEmpleado', params: {empleados_id:empleados.empleados_id} }">
               <i class="fa fa-eye">Ver Empleado</i>
             </router-link>
           </li>
    </ul>
     <div class="fixed-action-btn">
 <router-link to="/nuevoEmpleado" class="btn-floating btn-large red"> Nuevo
 </router-link>
     </div>
  </div>
</template>

<script>
import db from './firebaseinit'
export default {
  name: 'dashboard',
  data(){
    return {
 empleadoA: []
    }
  },
  created() {
  db.collection('Empleados').orderBy('NumeroEmp').get().then(querySnapshot => {
      querySnapshot.forEach(doc => {
        console.log(doc.data());
        const data = {
            'id': doc.id,
             'empleados_id': doc.data().NumeroEmp,
             'nombre': doc.data().Nombre,
             'departamento': doc.data().Departamento,
             'posicion': doc.data().sha
        }
        this.empleadoA.push(data)
      })
   })
  }
}
</script>



