<template >
<div id="editarE">
   <h2>Editar Empleados</h2>
   <div class="row">
   <form @submit.prevent="Actualizar" class="col s12">
     <div class="row">
       <div class="input-field col s12">
         <input type="number" v-model="empleados_id" required>
         <label>{{empleados_id}} </label>
       </div>
     </div>
     <div class="row">
       <div class="input-field col s12">
         <input type="text" v-model="nombre" required>
         <label>{{nombre}}</label>
       </div>
     </div>
     <div class="row">
       <div class="input-field col s12">
         <input type="text" v-model="departamento" required>
         <label>{{ departamento}}</label>
       </div>
     </div>
     <div class="row">
       <div class="input-field col s12">
         <input type="text" v-model="posicion" required>
         <label>{{posicion}}</label>
       </div>
     </div>
     <button type="submit" class="btn">Submit</button>
     <router-link to="/" class="btn grey">Cancel</router-link>
   </form>
 </div>
</div>
</template>

<script>
import db from './firebaseinit'
export default {
  name:'editarE',
  data (){
    return {
      empleados_id: null,
       Nombre: null,
       departamento: null,
       posicion: null
    }
  },  beforeRouteEnter (to, from, next) {
          db.collection('Empleados').where('NumeroEmp', '==', to.params.empleados_id).get().then((querySnapshot) => {
            querySnapshot.forEach((doc) => {
              next(vm => {
                vm.empleados_id = doc.data().NumeroEmp
                vm.nombre = doc.data().Nombre
                vm.posicion = doc.data().sha
                vm.departamento = doc.data().Departamento
              })
            })
          })
        },
        watch: {
        '$route':'fetchData'
      },
      methods: {
        fetchData () {
          db.collection('Empleados').where('NumeroEmp','==',this.$route.params.empleados_id).get().then((querySnapshot) => {
            querySnapshot.forEach((doc) => {
               this.empleados_id = doc.data().NumeroEmp
               this.nombre = doc.data().Nombre
               this.posicion = doc.data().sha
               this.departamento = doc.data().Departamento

            })
          })
        },
        Actualizar(){
          if(confirm ('¿Seguro de Actualizar?')) {
           db.collection('Empleados').where('NumeroEmp', '==', this.$route.params.empleados_id).get().then((querySnapshot) => {
             querySnapshot.forEach((doc) => {
               doc.ref.update({
                 NumeroEmp: this.empleados_id,
                 Nombre: this.nombre,
                 Departamento: this.departamento,
                 sha: this.posicion
               })
               .then(()=> {
                 this.$router.push({name:'mostrandoEmpleado',
                 params:{empleados_id: this.empleados_id}
               })
               })
             })
           })
          }
        }
  }

}
</script>



