<template>
  <div>
  <ul class="collection with-header">
      <li class="collection-header"><h4>{{nombre}}</h4></li>
      <li class="collection-item">Numero de id: {{empleados_id}}</li>
      <li class="collection-item">Departamento: {{departamento}}</li>
      <li class="collection-item">Posicion: {{posicion}}</li>
    </ul>

     <router-link to="/" class="btn grey"> Atras </router-link>
      <button @click="borrarEmpleado" class="btn red">Borrar</button>
      <div class="fixed-action-btn">
  <router-link v-bind:to="{name:'editarEmpleado', params: {empleados_id: empleados_id}}" class="btn-floating btn-large red">
    <i class="fa fa-pencil">Editar</i>
  </router-link>
      </div>
  </div>

</template>

<script>
import db from './firebaseinit'
export default {
  name: 'mostrandoEmpleado',
  data (){
    return{
      empleados_id:null,
      nombre:null,
      posicion:null,
      departamento:null
    }
  },
  beforeRouteEnter (to, from, next) {
        db.collection('Empleados').where('NumeroEmp', '==', to.params.empleados_id).get().then((querySnapshot) => {
          querySnapshot.forEach((doc) => {
            next(vm => {
              vm.empleados_id = doc.data().NumeroEmp
              vm.nombre = doc.data().Nombre
              vm.posicion = doc.data().sha
              vm.departamento = doc.data().Departamento
            })
          })
        })
      },
      watch: {
      '$route':'fetchData'
    },
    methods: {
      fetchData () {
        db.collection('Empleados').where('NumeroEmp','==',this.$route.params.empleados_id).get().then((querySnapshot) => {
          querySnapshot.forEach((doc) => {
             this.empleados_id = doc.data().NumeroEmp
             this.nombre = doc.data().Nombre
             this.posicion = doc.data().sha
             this.departamento = doc.data().Departamento

          })
        })
      },
      borrarEmpleado(){
        if(confirm ('Segudo de Borrar')) {
         db.collection('Empleados').where('NumeroEmp', '==', this.$route.params.empleados_id).get().then((querySnapshot) => {
           querySnapshot.forEach((doc) => {
             doc.ref.delete();
             this.$router.push('/')
           })
         })
        }
      }
}
}
</script>



